import md5 from '@/md5.js'
export default {
	data() {
		return {
			hotlist: [],
			userInfo:{}
		};
	},
	onLoad: function() {
		this.getlist();
		this.userInfo = this.$store.state;
	},
	methods: {
		getapi() {
			var timestamp = Date.parse(new Date());
			var api_key = "abcde";
			return [timestamp, md5(api_key + timestamp)];
			//返回api_key+时间戳md5加密
		},
		async getlist() {
			var key = await this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/books/getHot',
				data: {
					time: key[0],
					token: key[1]
				},
				success: (res) => {
					this.hotlist = res.data.hots;
					console.log(this.hotlist)
				}
			});
		},
		gotocomicdetails(id, uid) {
			uni.navigateTo({
				url: '../comicdetails/comicdetails?id=' + id + '&uid=' + uid
			})
		}
	}
}
